@import 'variables';

.portfolioPage{
  @include elementFlex(column, flex-start, center);
  width: 100%;
  min-height: 110vh;
  &__header{
    @include elementFlex(column, center, center);
    width: 100%;
    margin-top: 120px;
  }
  &__title{
    color: $mainColor;
    font-size: 3.7rem;
    line-height: 5.5rem;
  }
  &__subtitle{
    color: $fontColor;
    font-size: 1.6rem;
    font-weight: 400;
    text-align: center;
  }
  &__container{
    min-height: 200vh;
    margin-top: 30px;
    padding: 0 13%;
  }
  &__image{
    width: 100%;
    border-radius: 20px;
    margin-bottom: 15px;
    animation: fadeIn 0.3s forwards;
  }
  &__toTop{
    @include elementFlex(row, center, center);
    display: none;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    position: fixed;
    right: 50px;
    border: none;
    box-shadow: 0 0 15px rgb(179, 179, 179);
    bottom: 50px;
    background-color: #ffffff;
    &__icon{
      width: 40px;
      height: 40px;
    }
  }
}

.my-masonry-grid {
  display: -webkit-box; /* Not needed if autoprefixing */
  display: -ms-flexbox; /* Not needed if autoprefixing */
  display: flex;
  margin-left: -30px; /* gutter size offset */
  width: auto;
}
.my-masonry-grid_column {
  padding-left: 20px; /* gutter size */
  background-clip: padding-box;
}

/* Style your items */
.my-masonry-grid_column > div { /* change div to reference your elements you put in <Masonry> */
  background: grey;
  margin-bottom: 30px;
}

@media screen and (max-width: 1040px) {
    .portfolioPage{
      &__toTop{
        display: flex;
      }
    }
}

@media(max-width: 786px){
  .portfolioPage{
    &__container{
      padding: 0 7%;
    }
    &__subtitle{
      padding: 0 8%;
    }
  }
  .my-masonry-grid_column{
    padding-left: 8%;
  }
}

@media(max-width: 1550px){
  .portfolioPage{
    &__container{
      padding: 0 7%;
    }
    &__subtitle{
      padding: 0;
    }
  }
  .my-masonry-grid_column{
    padding-left: 25px;
  }
}

@keyframes fadeIn {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}